/* The styling below is to remove up/down arrows from input/number elements */
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.activity-form {
  display: flex;
  flex-direction: column;
  width: 25rem;
}

.activity-form__label {
  font-size: 1.25rem;
  margin: .5rem;
}

.activity-form__input {
  font-size: 1.25rem;
  line-height: 1.8rem;
  padding-left: .5rem;
  border-radius: 5px;
  outline: none;
  border: 1px solid transparent;
  box-shadow: 0 0 3px 1px;
}

.activity-form__input--short {
  font-size: 1.25rem;
  line-height: 2rem;
  padding-left: 1.5rem;
  margin-left: .5rem;
  width: 2rem;
  border-radius: 5px;
  outline: none;
  border: 1px solid transparent;
  box-shadow: 0 0 3px 1px;
}

.activity-form__input--short:focus {
  background: #ededed;
}
.activity-form__input:focus {
  background: #ededed;
}

.activity-form__row-grouping {
  margin-top: .75rem;
  flex-direction: row;
  text-align: center;
}

.activity-form__button {
  align-self: center;
  margin-top: 1.5rem;
  width: 20rem;
  height: 4rem;
  font-size: 1.5rem;
  background-color: #878787;
  color: white;
  cursor: pointer;
  outline: none;
  border: 1px solid transparent;
  transition: 0.4s;
}

.activity-form__button:hover {
  background-color: aliceblue;
  background: #ededed;
  color: black;
  box-shadow: 0 0 3px 1px;
}